import { Component, OnInit, ViewChild, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { SalesService } from '../../../../shared/services/sales.service';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { ErrorHandleService, ItemMasterService } from '../../../../shared';

@Component({
  selector: 'app-sales-add',
  templateUrl: './sales-add.component.html',
  styleUrls: ['./sales-add.component.scss']
})
export class SalesAddComponent implements OnInit {

  @ViewChild('dateInput') dateInput: jqxDateTimeInputComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Output() updateGrid = new EventEmitter();
  @Output() closeWindow = new EventEmitter();
  @Output() AddedData = new EventEmitter();


  invoiceForm: FormGroup;
  invoices: any;
  snNo: number = 1;

  outputItemAdapter: any = [];
  outputItemFocus: any = [];

  constructor(
    private fb: FormBuilder,
    private ss: SalesService,
    private ims: ItemMasterService,
    private ehs: ErrorHandleService,
  ) { }

  ngOnInit() {
    this.invoiceForm = this.fb.group({
      date: '',
      customer: [null, Validators.required],
      bill_no: [null, Validators.required],
      remarks: [null],
      shipping_details: [null, Validators.required],
      invoices: this.fb.array([this.createItems()])
    })
  }

  createItems(): FormGroup {
    return this.fb.group({
      sn: [{ value: this.snNo, disabled: true }, Validators.required],
      item_id: '',
      item_name: [null, Validators.required],
      quantity: [null, [
        Validators.required,
        Validators.pattern("^[0-9]*$")
      ]],
      rate: [null, Validators.required],
      amount: [null, Validators.required]
    })
  }

  addItem() {
    this.snNo++;
    this.invoices = this.invoiceForm.controls['invoices'] as FormArray;
    this.invoices.push(this.createItems());
  }

  removeItem(i) {
    if (this.invoices.length > 1) {
      this.snNo--;
      this.invoices.removeAt(i);
    }
  }

  save(value) {
    value.date = this.dateInput.getText();
    this.jqxLoader.open();
    this.ss.store(value).subscribe(
      result => {
        let data = this.ehs.formatError(result);
        if (data['result'] == true) {
          this.AddedData.emit(data);
          this.updateGrid.emit("test");
        } else {
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerHTML = '';
          for (let i = 0; i < data['message'].length; i++) {
            let errorDiv = document.createElement('li');
            errorDiv.innerText = data['message'][i];
            messageDiv.appendChild(errorDiv);
          }
          this.winNotification.open();
        }
        this.jqxLoader.close();
      }, error => {
        let data = this.ehs.formatError(error);
        let messageDiv: any = document.getElementById('windowMessage');
        messageDiv.innerText = data['message'];
        this.winNotification.open();
        this.jqxLoader.close();
      }
    )
  }

  cancelBtn() {
    this.closeWindow.emit();
  }

  public valueChanged($event, i) {
    let rate = this.invoiceForm.get('invoices')['controls'][i].get('rate').value;
    let quantity = this.invoiceForm.get('invoices')['controls'][i].get('quantity').value;

    if (rate && quantity) {
      let total = parseFloat(rate) * parseFloat(quantity);
      this.invoiceForm.get('invoices')['controls'][i].get('amount').patchValue(total);
    }

  }


  outputItemFilter(searchPr, index) {
    let keycode = searchPr['keyCode'];
    if ((keycode == 40)) {
      document.getElementById('itemCode2').focus();
    }
    let searchString = searchPr['target'].value;
    let len = searchString.length;
    let dataString = searchString.substr(len - 1, len);
    let temp = searchString.replace(' ', '');
    temp = temp.replace(/\s/g, '');
    if (dataString == ' ' && temp.length > 1) {
      if (temp) {
        this.outputItemFocus[index] = true;
        this.ims.index({ code: temp, transact: 1, limit: 'all' }).subscribe(
          response => {
            this.outputItemAdapter[index] = [];
            this.outputItemAdapter[index] = response;
            console.log(response);
          },
          error => {
            console.log(error);
          }
        );

      } else {
        this.outputItemFocus[index] = false;
      }
    }
  }

  outputItemListSelected(selectedEvent, index) {
    if (selectedEvent && selectedEvent.target && selectedEvent.target.value) {
      let displayText = selectedEvent.target.selectedOptions[0].text;
      this.invoiceForm.get('invoices')['controls'][index].get('item_id').patchValue(selectedEvent.target.value);
      this.invoiceForm.get('invoices')['controls'][index].get('item_name').patchValue(displayText);
      this.outputItemFocus[index] = false;
    }
  }

}



